import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { MaterialModule, MdToolbarModule, MdSidenavModule } from '@angular/material';
import { BrowserModule }  from '@angular/platform-browser';
import { HttpModule }  from '@angular/http';
import { AppComponent } from './app.component';
import { ChangelogDialog } from './changelog-dialog.component';
import { MusicsComponent } from './musics.component';
import { MusicChangelogComponent } from './music-changelog.component';

@NgModule({
  imports: [
    BrowserModule,
    HttpModule,
    MaterialModule.forRoot(),
    MdToolbarModule.forRoot(),
    MdSidenavModule.forRoot(),
    RouterModule.forRoot([
      {
        path: '',
        component: MusicsComponent,
      },
      {
        path: 'music-changelog',
        component: MusicChangelogComponent,
      },
    ]),
  ],
  declarations: [
    AppComponent,
    ChangelogDialog,
    MusicChangelogComponent,
    MusicsComponent,
  ],
  entryComponents: [
    ChangelogDialog,
  ],
  bootstrap: [ AppComponent ],
})
export class AppModule { }



